.header {
  display: flex;
  align-items: center;
  justify-content: center; 
  margin-top: 10px;
  font-size: 30px;
}

.logo {
  width: 60px;
  height: 60px;
  margin-left: 10px;
}

.title-prompt {
  margin-left: 80px;
  font-family: Arial, Helvetica, sans-serif;
}

.top-line {
  border-top: 3px solid rgb(0, 0, 0);
  height: 3px;
  width: 100%;
  margin-top: 80px;
}

.bottom-line {
  border-top: 3px solid rgb(0, 0, 0);
  height: 3px;
  width: 100%;
  margin-top: 35px;
}

.timeleft-prompt {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 20px; 
}

#timer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 20px; 
  font-size: 50px;
}

.button-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top: 20px;
}

.Back-btn,
.Confirm-btn {
  padding: 10px 20px;
  color: #fff;
  border: none;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  font-size: 16px;
  transition: background-color 0.3s, transform 0.3s;
  cursor: pointer;
  margin: 10px 0; /* Add space between buttons */
}

.Back-btn {
  background-color: #d31919;
}

.Back-btn:hover {
  background-color: #ff0101;
}

.Confirm-btn {
  background-color: #4caf50;
}

.Confirm-btn:hover {
  background-color: #51ca4f;
}
